
.photo1 {
    position: relative; 
    width: 500px;
    padding: 10px; /* Поля вокруг текста */
    margin: 0 auto;
    margin-bottom: 60px;
    background-color: white;
    border: 1px solid #d6d5d4;
    border-radius: 5px;
}

.photo1 img {
    width: 100%;
    height: auto;      
}

.photoOff {
    position: absolute;
    top: -105px;
    right: -470px;
    color: #e77161;
    background-color: #fff;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}

.photo-img{
    float: left;
    margin-right: 20px;
    min-height: 250px;
}

.photo-details { 
    list-style-type: none;
    display: inline-block;
}

.photo_profile {
    padding-top: 10px;
    padding-left: 18px;
}

.photo_profile-link {
    color: #717171;
    font-size: 17px;
    padding-left: 10px;
}

.avatar2 {
    max-width: 50px;
    max-height: auto;
    border-radius: 50%;
}

.photo_like2 {
    padding-top: 10px;
    padding-left: 15px;      
}

.photo_like-button2 {
    color: white;
    font-size: 16px;
    background-color:#48b4ce;
    border: none;
         
}

.photo_date2 {
    padding-top: 5px;
    padding-left: 15px;
    padding-bottom: 5px;
    color: #717171;
    font-size: 13px;
}